.xcui-progress{margin:10px}.xcui-progress .xcui-progress-container{width:100%;margin-right:-50px;padding-right:50px;display:inline-block}.xcui-progress .xcui-progress-outer{width:100%;background:#ccc;display:inline-block;border-radius:50px}.xcui-progress .xcui-progress-inner{background-color:#2db7f5;border-radius:50px;position:relative}.xcui-progress .xcui-progress-info{display:inline-block}.xcui-progress .xcui-progress-inner:after{content:"";opacity:0;position:absolute;top:0;right:0;bottom:0;left:0;background:#fff;border-radius:3px;-moz-animation:animate-shine 2s ease-out infinite;-webkit-animation:animate-shine 2s ease-out infinite}@-webkit-keyframes animate-shine{0%{opacity:0;width:0}50%{opacity:.5}100%{opacity:0;width:95%}}


