.calendar{width:240px;padding:10px;background:#fff;position:absolute;z-index:9999;left:0;top:38px;border:1px solid #DEDEDE;border-radius:2px;opacity:.95;-webkit-transition:all .5s ease;transition:all .5s ease}.calendar-enter .calendar-leave{opacity:0;-webkit-transform:translate3d(0, -10px, 0);transform:translate3d(0, -10px, 0)}.calendar-tit{text-align:center;margin:0 auto;width:104px;font-weight:bold}.calendar-tit input{color:#333;height:24px;outline:medium;text-align:center;border:none;background-color:transparent}.calendar-tit-year{width:36px}.calendar-tit-month{width:18px;margin-left:2px}.calendar:before{position:absolute;left:30px;top:-10px;content:"";border:5px solid rgba(0,0,0,0);border-bottom-color:#DEDEDE}.calendar:after{position:absolute;left:30px;top:-9px;content:"";border:5px solid rgba(0,0,0,0);border-bottom-color:#fff}.calendar-tools{height:32px;font-size:14px;line-height:32px;color:#333}.calendar-tools .float.left{float:left}.calendar-tools .float.right{float:right}.calendar-tools>i{margin:0 10px;line-height:32px;cursor:pointer;color:#707070}.calendar-tools>i:hover{color:#333}.calendar table{clear:both;width:100%;margin-bottom:10px;border-collapse:collapse;color:#444}.calendar td{margin:2px !important;padding:5px 0;width:14.28571429%;text-align:center;vertical-align:middle;font-size:14px;line-height:125%;cursor:pointer;border:0}.calendar td:hover{background:#f3f8fa}.calendar td.week{pointer-events:none !important;cursor:default !important}.calendar td.disabled{color:#c0c0c0;pointer-events:none !important;cursor:default !important}.calendar td.today{background-color:#46c3c1;color:#fff;font-size:14px;border-radius:4px}.calendar td.today .lunar{color:#fff}.calendar thead td{text-transform:uppercase}.calendar .timer{margin:10px 0 10px 30px;width:150px;text-align:center}.calendar .timer input{border-radius:2px;padding:5px;font-size:14px;line-height:18px;color:#46c3c1;width:50px;text-align:center;border:1px solid #efefef}.calendar .timer input:focus{border:1px solid #46c3c1}.calendar .timer-item{float:left;text-align:center;position:relative;cursor:pointer}.calendar .timer-item-current{color:#46c3c1;line-height:24px;font-size:14px}.calendar .timer label{margin:0;width:30px;padding:5px;line-height:14px;margin-right:4px;font-weight:normal}.calendar-button{text-align:center}.calendar-button button{border:none;cursor:pointer;display:inline-block;min-height:1em;vertical-align:baseline;background:#46c3c1;color:#fff;margin:0 .3em 0 0;padding:.6em 2em;font-size:1em;line-height:1em;text-align:center;border-radius:.3em}.calendar-button button.cancel{background:#efefef;color:#666}.calendar .lunar{font-size:11px;line-height:150%;color:#aaa}.calendar .drop-down{position:absolute;top:24px;left:-10px;width:50px;height:140px;background:#fff;border:1px solid #efefef;padding:0;margin:0;overflow-y:scroll}.calendar .drop-down li{width:50px;list-style:none}.calendar .drop-down li.on{background:#eee}.calendar .drop-down li:hover{background:#eee}.calendar .clearfix{zoom:1}.calendar .clearfix:after{display:block;width:0;height:0;visibility:hidden;content:'';clear:both}.bg-pr{position:relative}.btn-default{color:#666;border:#ccc solid 1px;background-color:#fff;margin-left:-1px;border-radius:0 4px 4px 0}


