.xcui-modal-wrapper{z-index:1000;overflow:auto}.xcui-modal-wrapper,.xcui-modal-mask{position:fixed;top:0;right:0;left:0;bottom:0;width:100%;height:100%;-webkit-transition:opacity .2s ease;transition:opacity .2s ease;background-color:rgba(55,55,55,0.6)}.xcui-modal{font-size:14px;position:relative;margin:0 auto;top:100px;background-color:#fff;padding:0;background-color:white;border-radius:2px;box-shadow:0 2px 8px 1;-webkit-transition:all .2s ease;transition:all .2s ease;max-height:100vh;max-width:100vw;overflow-x:hidden;overflow-y:auto}.xcui-modal.large{width:45rem}.xcui-modal-body{padding:1.6rem;border-bottom:1px solid #f3f3f3;min-height:8rem}.xcui-modal-header{font-size:1.6rem;padding:.6rem;border-bottom:1px solid #f3f3f3;text-align:center}.xcui-modal-header-close{position:absolute;right:8px;top:8px;color:#000;opacity:.2;font-weight:700;line-height:1;font-size:20px;outline:0}.xcui-modal-header-close:hover{cursor:pointer;text-decoration:none;opacity:.5}.xcui-modal-footer{padding:.5rem 1rem;text-align:center}.xcui-modal-footer .xcui-modal-btn{margin-right:.5rem;background-color:#46C3C1;color:#fff;border:#46c3c1 solid 1px;min-width:75px}.xcui-modal-footer .xcui-modal-btn:hover{background-color:#2b9d9b;border:#2b9d9b solid 1px}.xcui-modal-footer .xcui-modal-btn:active{background-color:#2b9d9b;border:#2b9d9b solid 1px;box-shadow:inset 0 3px 6px rgba(0,0,0,0.2)}.xcui-modal-footer .xcui-modal-cancel-btn{min-width:75px}.xcui-modal-size-small{width:22%}.xcui-modal-size-middle{width:30%}.xcui-modal-size-large{width:60%}.xcui-modal-size-full{width:99%}


