.toaster{width:300px;border-radius:5px;text-align:center;box-shadow:0 1px 8px rgba(99,99,99,0.2);padding:5px 10px;font-size:16px;z-index:1000;position:fixed;top:150px;left:calc(50% - 150px);background:#fff}.toaster .toaster-icon{vertical-align:middle}.toaster .toaster-desc{display:inline-block;margin-left:10px;margin-bottom:0;vertical-align:middle}


